.App {
  width: 100%;
  margin-top: 100px;
}
.input, .span, .select {
  display: inline-block;
  width: 98%;
  padding: 4px 12px 4px 0;
  margin-bottom: 1px;
  border: 1px dotted transparent;
  transition: background 0.1s ease-in-out, border-color 0.2s ease-in-out;
}
.input:hover, .select:hover, .input:focus, .select:focus {
  background: #fdf4db;
}
.input:focus, .select:focus {
  border-color: blue;
}
.input.white:hover, .select.white:hover, .input.white:focus, .select.white:focus {
  color: inherit;
}
.total__invoice{
  margin: 30px auto 50px auto;
}
.invoice-wrapper {
  position: relative;
  background: #fff;
  width: 700px;
  margin: 30px auto 50px auto;
  padding: 40px 35px;
  box-shadow: 0 0 17px 0 rgba(16, 40, 73, 0.09);
}
.download-pdf {
  position: fixed;
  top: 150px;
  margin-left: -110px;
  width: 40px;
  height: 40px;
  background: url('../src/images/download.svg');
  transition: opacity 0.2s ease-in-out;
}
.download-pdf.loading {
  opacity: 0.3;
}
.download-pdf a {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.mail{
  background: url('../src/images/gmail.png') no-repeat;
  position: fixed;
  top: 200px;
  margin-left: -110px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.icon {
  display: inline-block;
  position: relative;
  width: 16px;
  height: 16px;
  border: 0;
  border-radius: 50%;
}
.icon-absolute {
  position: absolute;
}
.icon-remove::before, .icon-remove::after {
  content: '';
  position: absolute;
  width: 2px;
  height: 10px;
  left: 7px;
  top: 3px;
  background-color: #fff;
  cursor: pointer;
}
.icon-remove::before {
  transform: rotate(45deg);
}
.icon-remove::after {
  transform: rotate(-45deg);
}
.icon-add::before, .icon-add::after {
  content: '';
  position: absolute;
  width: 2px;
  height: 10px;
  left: 7px;
  top: 3px;
  cursor: pointer;
  background-color: rgb(255, 255, 255);
}
.icon-add::after {
  transform: rotate(-90deg);
}
