body {
  font-family: Nunito, sans-serif;
  font-size: 14px;
  color: #555;
  background: #f2f3f5;
  padding: 0;
  margin: 0;
}
* {
  box-sizing: border-box;
}
::placeholder {
  color: #777;
  opacity: 1;
}
input, textarea, select {
  font-family: Nunito, sans-serif;
  font-size: 14px;
  color: #555;
  background: transparent;
  border-radius: 3px;
  outline: 0;
}
select {
  outline: 0;
}
textarea {
  resize: vertical;
  background: transparent;
}
.add__item .link {
  display: flex;
  align-items: center;
  background: transparent;
  cursor: pointer;
  border: 0;
  color: blue;
}
.add__item .link:hover, .add__item .link:focus {
  text-decoration: underline;
}
.add__item .link:focus {
  outline: 1px dotted cyan;
  opacity: 1;
}
